import Link from "next/link";
import { Suspense } from "react";
import { motion } from "framer-motion";
import {
  Sidebar,
  SidebarHeader,
  SidebarGroupContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarInset,
  SidebarProvider,
  SidebarTrigger,
} from "./ui/sidebar";

interface LayoutProps {
  children: React.ReactNode;
  nav: Array<{
    label: string;
    href: string;
    icon?: React.ReactNode;
  }>;
  pathname?: string;
  user?: {
    tenant_id?: string;
    name?: string;
    email?: string;
  };
  isAffiliate?: boolean;
  isAdmin?: boolean;
}

function Loading() {
  return (
    <div className="flex items-center justify-center min-h-screen">
      <div className="w-8 h-8 border-4 border-neutral-200 border-t-neutral-900 rounded-full animate-spin" />
    </div>
  );
}

export function Layout({
  children,
  nav,
  pathname,
  user,
  isAffiliate,
  isAdmin,
}: LayoutProps) {
  return (
    <div className="relative min-h-screen overflow-hidden bg-neutral-50">
      <SidebarProvider>
        <Sidebar className={`border-r fixed ${isAffiliate ? 'bg-gray-900 border-gray-800' : 'bg-white border-neutral-200'}`}>
          <SidebarHeader className={`flex h-16 items-center justify-center border-b px-6 ${isAffiliate ? 'border-gray-800' : 'border-neutral-200'}`}>
            {isAffiliate ? (
              <img src="/mesoblogo.jpeg" alt="Mesob Logo" width={80} height={80} className="rounded" />
            ) : (
              <div className="flex items-center gap-2 py-3 font-semibold text-neutral-900">
                Mesob
              </div>
            )}
          </SidebarHeader>

          <SidebarGroupContent className="p-4">
            <SidebarMenu className="space-y-2">
              {nav.map((item) => {
                const isActive = pathname === item.href || (item.href !== "/dashboard/affiliate" && pathname?.startsWith(item.href));
                const activeClasses = isAffiliate
                  ? "bg-emerald-600 text-white hover:bg-emerald-700"
                  : "bg-neutral-900 text-white hover:bg-neutral-800";
                const inactiveClasses = isAffiliate
                  ? "text-gray-300 hover:bg-gray-800 hover:text-white"
                  : "text-neutral-600 hover:bg-neutral-100";

                const handleClick = (e: React.MouseEvent) => {
                  if (item.label === "Sign out") {
                    e.preventDefault();
                    localStorage.removeItem('token');
                    localStorage.removeItem('access_token');
                    window.location.href = item.href;
                  }
                };

                return (
                  <SidebarMenuItem key={item.label}>
                    <SidebarMenuButton
                      asChild
                      isActive={isActive}
                      className={`w-full flex items-center gap-3 px-3 py-2.5 rounded-lg transition-colors duration-150 text-sm ${
                        isActive ? activeClasses : inactiveClasses
                      }`}
                    >
                      <Link
                        href={item.href}
                        onClick={handleClick}
                        className="flex w-full items-center gap-3"
                      >
                        {item?.icon}
                        <span>{item.label}</span>
                      </Link>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                );
              })}
            </SidebarMenu>
          </SidebarGroupContent>

          {/* User section at bottom */}
          <div className={`mt-auto p-4 border-t ${isAffiliate ? 'border-gray-800' : 'border-neutral-200'}`}>
            <div className="flex items-center gap-3 px-3 py-2">
              <div className={`w-8 h-8 rounded-full flex-shrink-0 ${isAffiliate ? 'bg-gray-700' : 'bg-neutral-200'}`} />
              <div className="flex-1 min-w-0">
                <p className={`text-sm truncate ${isAffiliate ? 'text-white' : 'text-neutral-900'}`}>
                  {user?.name || "User"}
                </p>
                <p className={`text-xs truncate ${isAffiliate ? 'text-gray-400' : 'text-neutral-500'}`}>
                  {user?.email || "user@example.com"}
                </p>
              </div>
            </div>
          </div>
        </Sidebar>

        <SidebarInset>
          <Suspense fallback={<Loading />}>
            <motion.div
              initial={{ opacity: 0, y: 24 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.45, ease: "easeOut" }}
              className="flex-1 bg-white shadow-sm"
            >
              <header className="sticky top-0 z-30 flex flex-col gap-3 border-b border-neutral-200 bg-white p-6 sm:flex-row sm:items-center sm:justify-between">
                <div className="flex items-center gap-4">
                  <SidebarTrigger className="text-neutral-600" />
                  <span className="text-neutral-900">
                    {nav.find((n) => pathname?.startsWith(n.href))?.label ||
                      "Overview"}
                  </span>
                </div>
                {!isAffiliate && !isAdmin && user?.tenant_id && (
                  <span className="inline-flex items-center rounded-full border border-neutral-200 bg-neutral-50 px-3 py-1 text-xs text-neutral-600">
                    Tenant: {user.tenant_id}
                  </span>
                )}
              </header>
              <main className="p-6 text-neutral-900">{children}</main>
            </motion.div>
          </Suspense>
        </SidebarInset>
      </SidebarProvider>
    </div>
  );
}
